.popup__info {
    width: 100%;
    max-width: 430px;
    background-color: #fff;
    border-radius: 10px;
    padding: 34px 36px 36px;
    box-sizing: border-box;
    position: relative;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.15);
    z-index: 3;
}

@media screen and (max-width: 767px) {
    .popup__info {
        max-width: 75vw;
        padding: 30px 20px;
    }
}